<template>
  <div class="navBar">

    <div class="header">
      <MobileMenuLogo v-bind:logo-image-src="logoUrl"></MobileMenuLogo>
      <MobileMenuHamburgerLight v-on:click.native="toggleMenuFullPage"></MobileMenuHamburgerLight>
    </div>

    <div id="menuMobile" ref="menuMobile">
      <MobileMenuHead></MobileMenuHead>
      <MobileMenuMain></MobileMenuMain>
      <MobileMenuFoot></MobileMenuFoot>
    </div>

  </div>

</template>

<script>

import MobileMenuLogo from "@/components/navBar/mobile/MobileMenuLogo";
import MobileMenuMain from "@/components/navBar/mobile/MobileMenuMain";
import MobileMenuHead from "@/components/navBar/mobile/MobileMenuHead";
import MobileMenuFoot from "@/components/navBar/mobile/MobileMenuFoot";
import MobileMenuHamburgerLight from "@/components/navBar/mobile/MobileMenuHamburgerTwoColors";

export default {
  name: "ViewMobileNavBarFullPage",
  props: [
    "logoUrl"
  ],
  components: {
    MobileMenuHamburgerLight,
    MobileMenuLogo,
    MobileMenuMain,
    MobileMenuHead,
    MobileMenuFoot
  },
  mounted() {

    // SCROLL BEHAVIOUR
    document.documentElement.style.overflow = "hidden"; // html scroll
    document.body.style.overflow = "hidden";            // body scroll

    // CALCULATE & SET INITIAL VH VALUE
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);

    // ADD LISTENER FOR VH VALUE IN CASE OF WINDOW RESIZING
    window.addEventListener('resize', () => {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
      if (window.innerWidth > 1023) {
        // eslint-disable-next-line no-undef
        let fullpage = fullpage_api
        document.documentElement.style.overflow = "hidden"; // html scroll
        document.body.style.overflow ="hidden";             // body scroll
        fullpage.setAllowScrolling(true)
        fullpage.setKeyboardScrolling(true)
      }
    });
  },
  methods: {
    toggleMenuFullPage() {

      let toggle = document.getElementById("hamburger")
      let menu = document.getElementById("menuMobile")
      let animatedMenuPoints = document.getElementsByClassName("animate")
      // eslint-disable-next-line no-undef
      let fullpage = fullpage_api

      if (toggle.classList.contains("is-active")) {
        toggle.classList.remove("is-active")
        menu.style.display = "none"
        fullpage.setAllowScrolling(true)
        fullpage.setKeyboardScrolling(true)
        for (let i = 0; i < animatedMenuPoints.length; i++) {
          animatedMenuPoints[i].classList.remove("move")
        }
      } else {
        toggle.classList.add("is-active")
        menu.style.display = "block"
        fullpage.setAllowScrolling(false)
        fullpage.setKeyboardScrolling(false)
        for (let i = 0; i < animatedMenuPoints.length; i++) {
          animatedMenuPoints[i].classList.add("move")
        }
      }
    },
  }
}

</script>

<style scoped>

.header {
  height: var(--nav-bar-height);
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--nav-bar-bg-transparent);
}

.header a {
  display: flex;
  align-items: center;
}

#menuMobile {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  display: none;
  position: absolute;
  width: 100%;
  margin: auto;
  overflow: hidden;
  z-index: 1000;
  letter-spacing: var(--menu-points-letter-spacing);
}

</style>