<template>

<div>
  <ViewDesktopNavBar v-if="desktopView"
                     nav-bar-color="background-color: var(--nav-bar-bg-transparent); color: var(--nav-bar-font-color-one);"
                     sub-points-bg-color="background-color: var(--sub-points-background-color-one);"
                     :logo-url="logoUrl"
  ></ViewDesktopNavBar>
  <ViewMobileNavBarFullPage v-else
                            :logo-url="logoUrl"
  ></ViewMobileNavBarFullPage>
</div>

</template>

<script>

import ViewDesktopNavBar from "@/components/navBar/desktop/ViewDesktopNavBar";
import ViewMobileNavBarFullPage from "@/components/navBar/mobile/ViewMobileNavBarFullPage";

export default {
  name: "NavBarTransparent",
  data() {
    return {
      desktopView: undefined,
      logoUrl: window.location.origin + '/logo/logo.png',
    }
  },
  components: {
    ViewMobileNavBarFullPage,
    ViewDesktopNavBar
  },
  mounted() {
    let vw = window.innerWidth;
    if (vw > 1023) { this.desktopView = true; }
    else           { this.desktopView = false; }

    window.addEventListener('resize', () => {
      vw = window.innerWidth;
      if (vw > 1023) { this.desktopView = true; }
      else           { this.desktopView = false; }
    });
  }
}

</script>

<style scoped>

</style>