<template>

<div class="navBarColored">
  <ViewDesktopNavBar v-if="desktopView"
                     nav-bar-color="background-color: var(--nav-bar-bg-color-header); color: var(--nav-bar-font-color-two);"
                     sub-points-bg-color="background-color: var(--sub-points-background-color-two);"
                     :logo-url="logoUrl"
  ></ViewDesktopNavBar>
  <ViewMobileNavBar v-else
                    :logo-url="logoUrl"
  ></ViewMobileNavBar>
</div>

</template>

<script>

import ViewDesktopNavBar from "@/components/navBar/desktop/ViewDesktopNavBar";
import ViewMobileNavBar from "@/components/navBar/mobile/ViewMobileNavBar";

export default {
  name: "NavBar",
  data() {
    return {
      desktopView: undefined,
      logoUrl: window.location.origin + '/logo/logo-dark.png',
    }
  },
  components: {
    ViewDesktopNavBar,
    ViewMobileNavBar,
  },
  mounted() {
    let vw = window.innerWidth;
    if (vw > 1023) { this.desktopView = true; }
    else           { this.desktopView = false; }

    window.addEventListener('resize', () => {
      vw = window.innerWidth;
      if (vw > 1023) { this.desktopView = true; }
      else           { this.desktopView = false; }
    });
  }
}

</script>

<style scoped>

.navBarColored {
  height: var(--nav-bar-height);
}

</style>